import {reactive,toRefs} from "vue";
interface List{
    id:number;
    title:string;
    path:string;
    phoneTitle:string;
    icon:string;
}

interface Data{
    list:List[];
}

export const useNavBar = ()=>{
    const data = reactive<Data>({
        list:[
            {id:0,title:"HOME",phoneTitle:"HOME",path:"/",icon:"icon-home"},
            {id:1,title:"ABOUT US",phoneTitle:"ABOUT",path:"/about",icon:"icon-about"},
            {id:2,title:"TRAILER FOR SALE",phoneTitle:"SALE",path:"/sale",icon:"icon-sale"},
            {id:3,title:"TRAILER FOR HIRE",phoneTitle:"HIRE",path:"/hire",icon:"icon-hired"},
        ]
    })

    return {
        ...toRefs(data),
    }
}