import { reactive, toRefs } from "vue";
import { ElMessage, ElLoading } from "element-plus";
import axios from "axios";
export const useContactUs = () => {
    const data = reactive({
        dataList: [
            { id: 0, img: require('@/assets/images/Trailer Website Design-03.png'), content: "0490 825 568" },
            { id: 1, img: require('@/assets/images/Trailer Website Design-04.png'), content: "norusttrailerau@gmail.com" },
            { id: 2, img: require('@/assets/images/Trailer Website Design-05.png'), content: "31 Howe St,Cairns North, QLD, 4870" },
        ],
        dataList2: [
            { id: 0, img: require('@/assets/images/instagram.png'), arrow: require('@/assets/images/Trailer Website Design-06.png') },
            { id: 1, img: require('@/assets/images/facebook.png'), arrow: require('@/assets/images/Trailer Website Design-06.png') },
            { id: 2, img: require('@/assets/images/messenger.png'), arrow: require('@/assets/images/Trailer Website Design-06.png') },
        ],
        ruleForm: {
            name: "",
            phone: "",
            email: "",
            message: "",
        }
    })

    const handleClick = (idx: number, content: string) => {
        if (idx === 0) {
            window.location.href = `tel://${content}`;
        } else {
            window.location.href = `mailto:${content}`;
        }
    }
    const onSubmit = () => {
        const { name, phone, email, message } = data.ruleForm;
        if (name.trim() && phone.trim() && email.trim() && message.trim()) {
            const loading = ElLoading.service({
                lock: true,
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)',
            });
            axios({
                url: 'https://us-central1-trailer-seller.cloudfunctions.net/sendMail',
                method: 'POST',
                data: {
                    name,
                    phone,
                    email,
                    message
                }
            })
                .then(res => {
                    if (res.data === "sended") {
                        loading.close();
                        ElMessage.success({
                            message: "We will get in touch ASAP!!!",
                            type: "success",
                        });
                        data.ruleForm = {
                            name: "",
                            phone: "",
                            email: "",
                            message: "",
                        };
                    } else {
                        loading.close();
                        ElMessage.warning({
                            message: 'Failure to send message!!',
                            type: 'warning'
                        });
                    }
                })
        } else {
            ElMessage.warning({
                message: 'Please complete your submit form',
                type: 'warning'
            });
        }
    }

    return {
        ...toRefs(data),
        handleClick,
        onSubmit
    }
}