import { reactive,toRefs } from "vue";

interface List {
    id:number;
    img:string;
    content:string;
}

interface Data{
    list:List[]
    isShow:boolean;
}


export const useTopBar = ()=>{
    const data = reactive<Data>({
        list:[
            {id:0,img:require("@/assets/images/Trailer Website Design-03.png"),content:"0490 825 568"},
            {id:1,img:require("@/assets/images/Trailer Website Design-04.png"),content:"norusttrailerau@gmail.com"}
        ],
        isShow:true,
    })
    const handleClick = (id:number,content:string)=>{
        if(id===0){
            window.location.href = `tel://${content}`;
        }else{
            window.location.href = `mailto:${content}`;
        }
    }
    const handleShow = ()=>{
        data.isShow = !data.isShow
    }
    return {
        ...toRefs(data),
        handleClick,
        handleShow
    }
}