import { reactive,toRefs } from "vue"

export const useHire = ()=>{
    const data = reactive({
        list:[
            {id:0,img:require("@/assets/images/rent/1.jpg")},
            {id:1,img:require("@/assets/images/rent/2.jpg")},
            {id:2,img:require("@/assets/images/rent/3.jpg")},
            {id:3,img:require("@/assets/images/rent/4.jpg")},
            {id:4,img:require("@/assets/images/rent/5.jpg")},
        
        ],
        index:0
    })
    const onMouseEnter = (idx:number)=>{
        data.index = idx;
    }
    return {
        ...toRefs(data),
        onMouseEnter
    }
}