import { ajaxTransport } from "jquery";
import {reactive,toRefs} from "vue";

interface List {
    id:number;
    img:string;
    title:string;
    content:string;
}

interface Data{
    list:List[],
    isShow:boolean;
    index:number;
}

export const useAbout = ()=>{
    const data = reactive<Data>({
        list:[
            {
                id:0,
                img:require("@/assets/images/1.jpg"),
                title:"Heavy Duty Design",
                content:"All our trailers are heavy-duty designed to ensure a high level of safety while transporting heavy materials. You can count on NO RUST TRAILERS in the case of reliability. "
            },
            {
                id:1,
                img:require("@/assets/images/2.jpg"),
                title:"Australian Standards",
                content:"As an Australian business, we can proudly say all our galvanized trailers are fully complied with Australian Vehicle design standards and we make sure to go beyond and above. We believe in our products, and we stand behind them"
            },
            {
                id:2,
                img:require("@/assets/images/3.jpg"),
                title:"We save you money",
                content:"For the quality we offer, you will be surprised by the price we charge for them. All our trailers are absolutely good value."
            },
        ],
        isShow:false,
        index:0
    })

    const handleMouse= (show:boolean,idx:number)=>{
        data.index = idx
        data.isShow = show;
    }
    return {
        ...toRefs(data),
        handleMouse
    }
}