import {reactive,toRefs} from "vue";
import $ from "jquery";



export const useApp = () => {
    const data = reactive({
        index:0,
    })
    const menuSelector = (i: number) => {
        const arr =['.home','.about','.sale','.hire','.contact-us'];
        data.index = i;
        $('html, body').animate({
            scrollTop:$(`${arr[i]}`).offset()!.top - $(".home").offset()!.top
        }, 500)
      
        console.log(data.index)
    }
    return {
        ...toRefs(data),
        menuSelector,
    }
}