import { reactive,toRefs } from "vue"

export const useSale = ()=>{
    const data = reactive({
        list:[
            {id:0,img:require("@/assets/images/sale/1.jpg"),},
            {id:1,img:require("@/assets/images/sale/2.jpg"),},
            {id:2,img:require("@/assets/images/sale/3.jpg"),},
            {id:3,img:require("@/assets/images/sale/4.jpg"),},
            {id:4,img:require("@/assets/images/sale/5.jpg"),},
            {id:5,img:require("@/assets/images/sale/6.jpg"),},
        ],
        index:0
    })
    const onMouseEnter = (idx:number)=>{
        data.index = idx;
    }
    return {
        ...toRefs(data),
        onMouseEnter
    }
}